  font-family: "Alice", serif;
  outline: none; }

:root {
  font-size: 16px; }

body {
  background: linear-gradient(white, #c1e5e7, #52b8bc, #36888c, #205153);
  overflow: hidden; }

#root {
  height: 100vh;
  width: 100vw; }

.turnDevice {
  display: none; }

@media screen and (max-device-width: 1023px) and (min-aspect-ratio: 13 / 9) and (orientation: landscape) {
  .turnDevice {
    background: #212121;
    display: block;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    overflow: hidden; } }

